.first-row {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   z-index: 2;
   background-color: #ffffff;
   border-bottom: 1px solid #dddddd;
   padding-bottom: 5px;
   /*box-shadow: 0 10px 6px -6px #777;*/
}
.collapsed .first-row {
   border: none;
   padding: 0;
}
.column {
   max-width: 60%;
   min-width: 20%;
   overflow: hidden;
   padding: 0;
   margin: 0 3px;
}
.column-40 {
   -webkit-box-flex: 2;
       -ms-flex-positive: 2;
           flex-grow: 2;
}
.details {
   /*display: block;
   opacity: 1;
   height: auto;
   transform: translateY(0px);
   transition: all 0.1s;*/
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-orient: horizontal;
   -webkit-box-direction: normal;
       -ms-flex-direction: row;
           flex-direction: row;
   padding: 5px 0px;
}
.hidden-row {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   /*padding: 5px 0px 0px 0px;*/
   -webkit-box-orient: vertical;
   -webkit-box-direction: normal;
       -ms-flex-direction: column;
           flex-direction: column;
}
.collapsed .hidden-row {
   display: none;
   /*height: 0;
   opacity: 0;
   transform: translateY(-20px);*/
}
.values, .receipt {
   width: 50%;
}
.list-item footer {
   border-top: 1px solid #dddddd;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: end;
       -ms-flex-pack: end;
           justify-content: flex-end;
   width: 100%;
}
.list-item img {
   width: 100%;
}
.list-item h6 {
   margin: 0;
   color: #aaaaaa;
   /*text-transform: uppercase;*/
   font-weight: 300;
   letter-spacing: 0.1rem;
   font-size: 0.7rem;
}
.list-item p {
   margin: 0;
}
.collapsed p {
   height: 1.5rem;
   overflow: hidden;
   white-space: nowrap;
   text-overflow: ellipsis;
}



