
.add-transaction-form {
  margin-bottom: 50px;
}
input[type="text"], input[type="number"], input[type="date"], textarea {
  border: 1px solid #dddddd;
  width: 100%;
  font-size: 1.25rem;
  /*min-height: 1.75rem;*/
}
.half-width {
  width: calc(50% - 5px);
  display: inline-block;
}
.half-width:last-of-type {
  margin-left: 15px;
  background-color:blue;
}
label {
  margin-bottom: 3px;
}
section {
  padding-bottom: 10px;
}
.camera-input {
  /*display: flex;
  justify-content: center;
  align-items: center;*/
  height: 100px;
  margin: 20px 0px;
}
.submit-button {
  width: 100%;
  border: 2px solid #8c1515;
  background-color: #8c1515;
  color: #FFFFFF;
  font-size: 1.3rem;
  -webkit-box-shadow: none;
          box-shadow: none;
}



