
body {
  margin: 0;
}
nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  position: fixed;
  bottom: 0;
  width: 100vw;
  /*background-color: #4366ee;*/
  border-top: 1px solid #dddddd;
  z-index: 5;
}
nav button {
  width: 33vw;
  border: none;
  outline: none;
  cursor: pointer;
  height: 50px;
  background-color: #ffffff;
  font-size: 1rem;
}
nav button a {
  text-decoration: none;
  color: #000000;
}
.add-button {
  border-left: 1px solid #dddddd;
  border-right: 1px solid #dddddd;
}




